@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");
:root {
  --font-family: Roboto, Arial, Helvetica, sans-serif; }

html, body {
  min-height: 100vh;
  display: flex;
  flex-direction: column; }

main {
  flex-grow: 1; }

.c-about-dialog {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.3); }
  .c-about-dialog > div {
    max-width: 400px; }

.c-is-dialog-hidden {
  display: none; }

[draggable] {
  user-select: none;
  -khtml-user-drag: element;
  -webkit-user-drag: element; }

#tasklist {
  list-style-type: none;
  margin: 0 24px;
  padding: 0; }
  #tasklist li {
    padding: 0 4px; }
    #tasklist li input[type=checkbox]:checked + label {
      text-decoration: line-through; }
